
.navbar-speaker {
    background: #4b295c;
    .nav {
        width: 80%; } }
.speaker-content {
    align-items: center;
    background-color: #f3f7f8;
    display: flex;
    flex-flow: column;
    justify-content: cener;
    padding: 5em 3em;
    width: 100%;
    @media only screen and (max-width: 1112px) {
        padding: 5em 1em; }
    @media only screen and (max-width: 580px) {
        padding: 5em 0; }
    .speaker-card {
        background-color: #fff;
        width: 80%;
        padding: 2em;
        @media only screen and (max-width: 1112px) {
            padding: 1em;
            width: 100%; }
        .speaker-head {
            align-items: center;
            display: flex;
            flex-flow: column;
            width: 100%;
            .img {
                display: block;
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                border-radius: 50%;
                height: 12em;
                width: 12em;
                min-width: 12em; }
            .speaker-col {
                align-items: center;
                display: flex;
                flex-flow: column;
                justify-content: center;
                padding: 1em;
                width: 100%;
                .speaker-name {
                    font-size: 1.2em;
                    line-height: 1.45em !important;
                    margin: 1em 1em .8em 1em;
                    text-align: center; }
                .speaker-position {
                    color: #868688;
                    font-style: italic;
                    font-weight: 500;
                    font-size: .8em;
                    text-align: center;
                    margin: 0 1em 1em 1.45em; }
                .speaker-social {
                    a {
                        color: #333333;
                        font-size: 1.8em; } }
                .speaker-desc {
                    color: #5e5e5e;
                    font-weight: 500;
                    font-size: 1em;
                    text-align: left;
                    margin: 1.5em 1em 1.5em 1em;
                    width: 70%;
                    h1 {
                        font-size: 2em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    h2 {
                        font-size: 1.8em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    h3 {
                        font-size: 1.6em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    h4 {
                        font-size: 1.4em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    h5 {
                        font-size: 1.2em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    h6 {
                        font-size: 1em;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        width: 100%; }
                    p {
                        margin-bottom: .5em;
                        width: 100%;
                        strong {
                            font-family: "Karla", Arial, sans-serif;
                            font-weight: bold !important; }
                        a {
                            color: #553363; }
                        img {
                            height: auto;
                            width: auto;
                            max-width: 100%; } }

                    blockquote {
                        border-left: solid .28em #999999;
                        padding-left: .5em;
                        p {
                            font-style: italic; } }
                    ol, ul {
                        padding: 1em;
                        li {
                            font-family: "Karla", Arial, sans-serif;
                            font-size: 16px;
                            font-weight: normal;
                            line-height: 1.5em;
                            letter-spacing: 0.03rem;
                            color: #5e5e5e; } }
                    code {
                        background-color: #f6f8fa;
                        border-radius: 3px;
                        font-size: 85%;
                        line-height: 1.45;
                        margin-bottom: .3em;
                        overflow: auto;
                        padding: 16px; }

                    @media only screen and (max-width: 1112px) {
                        width: 100%; } }
                .training-title {
                    color: #553265;
                    font-size: 2em;
                    font-weight: 600;
                    letter-spacing: .1em;
                    text-align: center;
                    text-transform: uppercase;
                    width: 100%; }
                .speaker-training {
                    color: #553363;
                    display: flex;
                    flex-flow: row;
                    font-family: 'Montserrat', sans-serif !important;
                    font-size: 1.2em;
                    font-weight: 600;
                    line-height: 1.45em !important;
                    margin: 0 1em 1em 1em;
                    text-align: center;
                    width: 70%;
                    @media only screen and (max-width: 1112px) {
                        align-items: flex-start;
                        flex-flow: column;
                        width: 100%; }
                    a {
                        color: #553363;
                        margin-left: .5em;
                        text-decoration: underline; } }
                .speaker-talk {
                    color: #553363;
                    display: flex;
                    flex-flow: row;
                    font-family: 'Montserrat', sans-serif !important;
                    font-size: 1.2em;
                    font-weight: 500;
                    line-height: 1.45em !important;
                    margin: 0 1em .5em 1em;
                    text-align: center;
                    width: 70%;
                    @media only screen and (max-width: 1112px) {
                        align-items: flex-start;
                        flex-flow: column;
                        width: 100%; }
                    strong {
                        font-weight: 600;
                        margin-right: .4em; }
                    a {
                        color: #553363;
                        margin-left: .5em;
                        text-decoration: underline; } } } } } }
